<template>
  <div>
    <b-navbar toggleable="lg" type="dark" variant="primary">
      <b-navbar-brand href="#">
        <router-link to="/">
          <img
            src="@/assets/logo.png"
            class="d-inline-block align-middle"
            width="80px"
            alt="Kitten"
          />
        </router-link>
      </b-navbar-brand>

      <b-navbar-toggle target="nav-collapse"></b-navbar-toggle>

      <b-collapse id="nav-collapse" is-nav>
        <b-navbar-nav class="but">
          <div>
            <b-button v-b-modal.modal-prevent-closing>회원가입</b-button>
            <b-modal id="modal-prevent-closing" ref="modal" title="회원가입" hide-footer>
              <form ref="form">
                <b-form-group class="signupmodal" label="이름" label-for="name-input">
                  <b-form-input id="name-input" v-model="signupCredential.userName"></b-form-input>
                </b-form-group>
              </form>
              <form ref="form">
                <b-form-group class="signupmodal" label="아이디" label-for="Id-input">
                  <b-form-input id="name-input" v-model="signupCredential.userId"></b-form-input>
                  <b-button class="mt-3" @click="isIdExist(signupCredential.userId)"> ID 중복확인 </b-button>
                </b-form-group>
              </form>
              <form ref="form">
                <b-form-group class="signupmodal" label="비밀번호" label-for="password-input">
                  <b-form-input type="password" id="password-input" v-model="signupCredential.password"></b-form-input>
                </b-form-group>
              </form>
              <form ref="form">
                <b-form-group class="signupmodal" label="비밀번호 확인" label-for="passwordConfirmation-input">
                  <b-form-input type="password" id="passwordConfirmation-input" v-model="signupCredential.passwordConfirmation"></b-form-input>
                </b-form-group>
              </form>
              <hr>
              <b-button @click="signupIsValid(signupCredential)"> 가입하기 </b-button>
            </b-modal>
            <b-button variant="success"
              ><router-link :to="{ name: 'Naver' }"
                >네이버</router-link
              ></b-button
            >
            <b-button v-b-modal.modal-2 variant="secondary">로그인</b-button>
            <b-modal id="modal-2" title="로그인">
              <div class="my-4">
                <label for="username">id: </label>
                <input
                  type="text"
                  id="username"
                  v-model="credentials.username"
                />
              </div>
              <v-form
                ref="form"
                v-model="valid"
                lazy-validation
              >
                <v-text-field
                  v-model="name"
                  :counter="17"
                  :rules="nameRules"
                  label="Name"
                  required
                ></v-text-field>
              </v-form>

              <div class="my-4">
                <label for="password">password: </label>
                <input
                  type="password"
                  id="password"
                  v-model="credentials.password"
                />
              </div>
              <v-form
                ref="form"
                v-model="valid"
                lazy-validation
              >
                <v-password-field
                  v-model="password"
                  :counter="17"
                  :rules="passwordRules"
                  label="password"
                  required
                ></v-password-field>
              </v-form>


              <div>
                <button v-bind:disabled="!isUsernameValid || !password" @click="loginIsValid(credentials)">로그인</button>
              </div>
            </b-modal>
          </div>
        </b-navbar-nav>
      </b-collapse>
    </b-navbar>
    <div class="icon-bar">
      <router-link to="/"
        ><b-iconstack font-scale="1" animation="spin">
          <b-icon
            stacked
            icon="camera"
            variant="info"
            scale="0.75"
            shift-v="-0.25"
          ></b-icon>
          <b-icon
            stacked
            icon="slash-circle"
            variant="danger"
          ></b-icon> </b-iconstack
      ></router-link>
      <router-link to="/about"><b-icon-bookmark-dash></b-icon-bookmark-dash></router-link>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import Swal from 'sweetalert2'
export default {
  name: "Navibar",
  data() {
    return {
      signupCredential: {
        userName: "",
        userId: "",
        password: "",
        passwordConfirmation: "",
      },
      credentials: {
        username: null,
        passsword: null,
      },
    }
  },
  methods: {
    signupIsValid: function (cred) {
      console.log(cred)
      if (cred.username === '' || cred.password === '' || cred.passwordConfirmation === '' || cred.userid) {
        Swal.fire({
          icon: 'error',
          title: 'Stop!',
          text: '이름, 아이디, 비밀번호, 비밀번호 확인란은 필수 입력사항입니다.',
        })
      } else if (cred.password !== cred.passwordConfirmation) {
        Swal.fire({
          icon: 'error',
          title: 'PasswordNotMatch',
          text: '비밀번호가 일치하지 않습니다',
        })
      } else if (cred.userName.length > 30) {
        Swal.fire({
          icon: 'error',
          title: 'UsernameError',
          text: '이름은 30자를 넘을 수 없습니다.'
        })
      } else if (cred.userId.length > 16) {
        Swal.fire({
          icon: 'error',
          title: 'IdError',
          text: '아이디는 16자를 넘을 수 없습니다 '
        })
      } else if ( 16 < cred.password || 9 > cred.password || 16 < cred.passwordConfirmation || 9 > cred.passwordConfirmation ) {
        Swal.fire({
          icon: 'error',
          title: '비밀번호 길이 오류',
          text: '비밀번호(확인)는 9자 이상 16자 이하여야 합니다.'
        })
      } else if ( cred.password.search(/[0-9]/g) < 0 || cred.password.search(/[a-z]/ig) < 0 || cred.password.search(/[`~!@#$%^&*/?;:]/gi) < 0) {
        Swal.fire({
          icon: 'error',
          title: '비밀번호 유형 오류',
          text: '비밀번호는 영문자, 숫자, 특수문자를 포함해야 합니다.'
        })
      } else {
        console.log(cred)
      }
    },
    isIdExist: function(id) {
      console.log(id)
    },

    loginIsValid: function () {
      axios({
        method: "post",
        url: 'http://localhost:8080/api-token-auth/',
        data: this.credentials,
      })
        .then((res) => {
          console.log(res);
          localStorage.setItem("jwt", res.data.token);
          this.$emit("changeLogin");
          this.$router.push({ name: "Main" });
        })
        .catch((err) => {
          console.log(err);
          alert("필수 입력 항목입니다.");
        });
    },

  },
}
</script>

<style>
.but {
  color: white;
  margin: 10px;
}
.icon-bar {
  height: 100%;
  width: 90px;
  background-color: #555;
  position: sticky; /* Fixed Sidebar (stay in place on scroll) */
  /*z-index: 1;*/ /* Stay on top */
  bottom: 0; /* Stay at the top */
  left: 0;
  overflow-x: hidden; /* Disable horizontal scroll */
  padding-top: 0;
}

.icon-bar a {
  display: block;
  text-align: center;
  padding: 16px;
  transition: all 0.3s ease;
  color: white;
  font-size: 36px;
}

.icon-bar a:hover {
  background-color: #858585;
}

.active {
  background-color: #5eaf13;
}

.signupmodal {
  margin: 2rem;
  font-size: 1.8rem;
}
</style>
